import { GET_ADMINS, CREATE_ADMINS, DELETE_ADMINS } from "./actionTypes";

export const getAdminsAction = (payload) => ({
  type: GET_ADMINS,
  payload,
});

export const createAdminsAction = (payload) => ({
  type: CREATE_ADMINS,
  payload,
});

export const deleteAdminsAction = (payload) => ({
  type: DELETE_ADMINS,
  payload,
});
