//-----------Basic imports----------------------------
import React from "react";
import styles from "./CategoriesList.module.scss";

//-----------Libraries----------------------------
import { useSelector, useDispatch } from "react-redux";
import { useHistory } from "react-router";
import clsx from "clsx";

//------------Components-----------------------------
import { Button, Loader } from "../../../components";

//------------Assets-----------------------------
import ICONS from "../../../assets/icons";

//------------APIs-----------------------------
import { getCategories } from "../../../APIs/categories";

//------------Redux action creators-----------------------------
import {
  editCategoriesAction,
  getCategoriesAction,
} from "../../../redux/actions/categoriesActions";

//-------------Rutes--------------------------------------------------
import ROUTES from "../../../route/routes";

const CategoryItem = ({ data = {} }) => {
  //-------------Data-----------------------------------

  const { _id = "", name = "", categories = [], photo = "" } = data;

  //-----------Hooks------------------------------------

  const [showMore, setShowMore] = React.useState(false);

  const dispatch = useDispatch();

  const history = useHistory();

  //----------Handlers------------------------------

  const itemClickHandler = () => setShowMore((prev) => !prev);

  const editClickHandler = () => {
    dispatch(editCategoriesAction({ categoryToEdit: data }));
    history.push(ROUTES.categories.addCategory);
  };

  //---------Layout--------------------------------

  return (
    <li className={clsx(styles.categoriesList__item, showMore && "more")}>
      <div
        className={styles.categoriesList__itemTop}
        onClick={itemClickHandler}
      >
        <div className={styles.categoriesList__itemLeft}>
          <div className={styles.categoriesList__itemImage}></div>
          <div className={styles.categoriesList__itemInfo}>
            <h2>{name}</h2>
            <span>Subcategories ({categories.length})</span>
          </div>
        </div>
        <button>{ICONS.utils.smallArrow}</button>
      </div>

      {showMore && (
        <div className={styles.categoriesList__itemMoreInfo}>
          <ul>
            {categories.map((item) => (
              <li key={item._id} className={styles.categoriesList__subcategory}>
                {item.name}
              </li>
            ))}
          </ul>
          <div>
            <Button onClick={editClickHandler}>
              <span>Edit</span>
            </Button>
          </div>
        </div>
      )}
    </li>
  );
};

const CategoriesList = () => {
  //-----------Hooks----------------------------

  const { loading, categories, loadMoreCategories } = useSelector(
    (state) => state.categoriesReducer
  );
  const dispatch = useDispatch();

  //-----------Handlers--------------------------

  const loadMoreHandler = () => dispatch(getCategories());

  React.useEffect(() => {
    dispatch(getCategoriesAction({ categoriesPage: 1 }));
    dispatch(getCategories());
  }, []);

  //-----------Layout----------------------------

  return (
    <div className={styles.categoriesList}>
      <ul>
        {categories.map((item) => (
          <CategoryItem key={item._id} data={item} />
        ))}
      </ul>
      {loading ? (
        <div className={styles.categoriesList__loader}>
          <Loader />
        </div>
      ) : (
        loadMoreCategories && (
          <Button onClick={loadMoreHandler}>
            <span>Load more</span>
          </Button>
        )
      )}
    </div>
  );
};

export default CategoriesList;
