//---------------------Basic imports-----------------------
import React from "react";
import "./Button.scss";

//---------------------Libraries-----------------------
import clsx from "clsx";

const Button = ({ children, onClick, className, type = "button" }) => {
  //---------------------Layout-----------------------

  return (
    <button
      type={type}
      className={clsx("button", className && className)}
      onClick={onClick}
    >
      {children}
    </button>
  );
};

export default Button;
