//-----------Basic imports----------------------------
import React from "react";
import styles from "./Admins.module.scss";

//-----------Libraries----------------------------
import { NavLink, Route, Switch, Redirect } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";

//-----------Routes----------------------------
import ROUTES from "../../route/routes";

//---------------Subpages---------------------------
import AdminsList from "./AdminsList";
import AddAdmin from "./AddAdmin";

const Admins = () => {
  //-----------Hooks----------------------------

  const { loading } = useSelector((state) => state.authReducer);
  const dispatch = useDispatch();

  //-----------Data----------------------------

  const tabs = [
    {
      name: "Create admin",
      path: ROUTES.admins.addAdmin,
    },
    {
      name: "Admins",
      path: ROUTES.admins.list,
    },
  ];

  //-----------Handlers----------------------------

  //-----------Layout------------------------------

  return (
    <div className={styles.admins}>
      <div className={styles.admins__nav}>
        {tabs.map((tab, index) => (
          <NavLink className={styles.admins__navItem} to={tab.path} key={index}>
            {tab.name}
          </NavLink>
        ))}
      </div>
      <Switch>
        <Route exact path={ROUTES.admins.page}>
          <Redirect to={ROUTES.admins.list} />
        </Route>
        <Route path={ROUTES.admins.list} component={AdminsList} />
        <Route path={ROUTES.admins.addAdmin} component={AddAdmin} />
      </Switch>
    </div>
  );
};

export default Admins;
