//-----------Basic imports----------------------------
import React from "react";
import styles from "./Home.module.scss";

//-----------Libraries----------------------------
import { NavLink, Route, useLocation, Switch } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import clsx from "clsx";

//-----------Dashboard pages---------------------------
import {
  Admins,
  Categories,
  PROs,
  Statistics,
  Users,
} from "../../dashboardSection";

//----------Components----------------------------------------------
import { Loader } from "../../components";

//-----------Assets----------------------------
import ICONS from "../../assets/icons";

//-----------Routes----------------------------
import ROUTES from "../../route/routes";

//-----------APIs----------------------------
import { logOut } from "../../APIs/Auth";

//-------------Helpers----------------------------
import { renderPageTitle } from "../../helpers";

const Home = () => {
  //-----------Hooks----------------------------
  const location = useLocation();

  const [state, setState] = React.useState({
    showNavBar: false,
  });

  const { showNavBar } = state;

  const { loading } = useSelector((state) => state.authReducer);
  const dispatch = useDispatch();

  //-----------Data----------------------------

  const tabs = [
    {
      name: "Statistics",
      path: ROUTES.statistics,
      icon: ICONS.navBar.statistics,
    },
    {
      name: "Users management",
      path: ROUTES.users,
      icon: ICONS.navBar.users,
    },
    {
      name: "PROs management",
      path: ROUTES.PROs.page,
      icon: ICONS.navBar.PROs,
    },
    {
      name: "Categories",
      icon: ICONS.navBar.categories,
      path: ROUTES.categories.page,
    },
    {
      name: "Admins",
      icon: ICONS.navBar.admins,
      path: ROUTES.admins.page,
    },
  ];

  //-----------Handlers----------------------------

  const menuClickHandler = () =>
    setState((prev) => ({ ...prev, showNavBar: !prev.showNavBar }));

  const tabClickHandler = () =>
    setState((prev) => ({ ...prev, showNavBar: false }));

  const logOutBtnHandler = () => dispatch(logOut());

  //-----------Layout----------------------------

  return (
    <div className={styles.home}>
      <div className={styles.home__box}>
        <div
          className={clsx(
            styles.home__menu,
            showNavBar && styles.home__menu_active
          )}
        >
          <div className={styles.home__menuLogo}>InstaPro</div>
          <ul>
            {tabs.map((tab, index) => (
              <li
                className={clsx(tab.name === "Admins" && "admins")}
                onClick={tabClickHandler}
                key={index}
              >
                {tab?.path ? (
                  <NavLink to={tab.path} activeClassName={styles.active}>
                    {tab.icon}
                    {tab.name}
                  </NavLink>
                ) : (
                  <span>
                    {tab.icon}
                    {tab.name}
                  </span>
                )}
              </li>
            ))}
          </ul>
          <div className={styles.home__menuFooter}>
            <div className={styles.home__menuLogout} onClick={logOutBtnHandler}>
              {loading ? (
                <div className={styles.home__menuLoading}>
                  <Loader />
                </div>
              ) : (
                <span>
                  {ICONS.navBar.logOut}
                  Log out
                </span>
              )}
            </div>
            <div className={styles.home__menuInfo}>
              <span className={styles.home__menuUsername}>email@gmail.com</span>
              <span className={styles.home__menuUserrole}>Super Admin</span>
            </div>
          </div>
        </div>

        <div className={styles.home__right}>
          <div className={styles.home__header}>
            <button
              onClick={menuClickHandler}
              className={clsx(
                styles.home__menuIcon,
                state.showNavBar && styles.home__menuIcon_active
              )}
            >
              <div></div>
              <div></div>
              <div></div>
            </button>
            <div>
              <h3 className={styles.home__suptitle}>Hi Sunny!</h3>
              <h2 className={styles.home__title}>
                {renderPageTitle(location.pathname)}
              </h2>
            </div>
          </div>

          <Switch>
            <Route path={ROUTES.categories.page} component={Categories} />
            <Route path={ROUTES.admins.page} component={Admins} />
            <Route path={ROUTES.PROs.page} component={PROs} />
            <Route path={ROUTES.users} component={Users} />
            <Route path={ROUTES.statistics} component={Statistics} />
          </Switch>
        </div>
      </div>
    </div>
  );
};

export default Home;
