//-------------------Basic imports------------------------------------------------
import React from "react";
import { Switch, Route, Redirect } from "react-router-dom";

//-------------------Pages------------------------------------------------
import { Home, SignIn } from "../pages";

//-------------------Routes------------------------------------------------
import ROUTES from "./routes";

const PrivateRoute = ({ children, redirectIf, redirectTo, ...props }) => (
  <Route
    {...props}
    render={({ location }) =>
      redirectIf ? (
        <Redirect to={{ pathname: redirectTo, from: location }} />
      ) : (
        children
      )
    }
  />
);

function MainRouter() {
  //-------------------Hooks------------------------------------------------

  const [token, setToken] = React.useState(localStorage.getItem("accessToken"));

  React.useEffect(() => {
    window.addEventListener("storage", () => {
      setToken(localStorage.getItem("accessToken"));
    });
  }, []);

  //-------------------Layout------------------------------------------------

  return (
    <>
      <Switch>
        <PrivateRoute
          exact
          path={ROUTES.signIn}
          redirectIf={token ? true : false}
          redirectTo={ROUTES.categories}
        >
          <SignIn />
        </PrivateRoute>
        {token ? (
          <Route path={ROUTES.home} component={Home} />
        ) : (
          <Redirect to={ROUTES.signIn} />
        )}
        <Route path={ROUTES.notFound}>
          {token ? (
            <Redirect to={ROUTES.statistics} />
          ) : (
            <Redirect to={ROUTES.signIn} />
          )}
        </Route>
      </Switch>
    </>
  );
}

export default MainRouter;
