//---------------------Basic imports-----------------------
import React from "react";
import styles from "./SignIn.module.scss";

//----------------------Libraries---------------------------
import { Formik } from "formik";
import { useDispatch, useSelector } from "react-redux";

//--------------------Validations---------------------------
import VALIDATIONS from "../../validation";

//----------------------Components--------------------------
import { Input, Button, Loader } from "../../components";

//----------------------Auth--------------------------
import { auth } from "../../APIs/Auth";

const SignIn = () => {
  //---------------------Hooks-----------------------

  const dispatch = useDispatch();
  const { loading } = useSelector((state) => state.authReducer);

  //---------------------Btn handlers-----------------------

  const submitHandler = (values) => dispatch(auth(values));

  //---------------------Layout-----------------------

  return (
    <div className={styles.signIn}>
      <Formik
        validateOnBlur={false}
        validateOnChange={false}
        initialValues={{ login: "", password: "" }}
        validationSchema={VALIDATIONS.loginSchema}
        onSubmit={submitHandler}
      >
        {({ errors, values, setFieldValue, handleSubmit }) => (
          <form onSubmit={handleSubmit} className={styles.signIn__inner}>
            <div className={styles.signIn__title}>Login to your account</div>
            <div className={styles.signIn__form}>
              <Input
                label="Login"
                formHelper
                placeholder="Enter login or e-mail"
                name="login"
                value={values.login}
                error={errors.login}
                onChange={setFieldValue}
              />
              <Input
                label="Password"
                formHelper
                placeholder="Enter password"
                name="password"
                type="password"
                value={values.password}
                error={errors.password}
                onChange={setFieldValue}
              />
              {loading ? (
                <div className={styles.signIn__loader}>
                  <Loader />
                </div>
              ) : (
                <Button type="submit">
                  <span>Sign In</span>
                </Button>
              )}
            </div>
          </form>
        )}
      </Formik>
    </div>
  );
};

export default SignIn;
