//-----------Basic imports----------------------------
import React from "react";
import styles from "./Users.module.scss";

//-----------Libraries----------------------------
import { useSelector, useDispatch } from "react-redux";

//---------------Components------------------------------
import {
  SearchInput,
  SelectInput,
  Button,
  Loader,
  UsersCard,
} from "../../components";

const Users = () => {
  //-----------Hooks----------------------------

  const { loading, loadMoreAdmins } = useSelector((state) => state.authReducer);
  const dispatch = useDispatch();

  //-----------Data----------------------------

  const infoTabs = [
    "Full Name",
    "Address",
    "Phone Number",
    "Email address",
    "Date",
    "Rating",
    "Delete",
  ];

  //-----------Handlers----------------------------

  //-----------Layout------------------------------

  return (
    <div className={styles.users}>
      <div className={styles.users__header}>
        <SearchInput placeholder="Search in users" />
        <SelectInput options={["Date", "Votes"]} title="Sort by" />
      </div>

      <div className={styles.users__infoBar}>
        {infoTabs.map((item, index) => (
          <p key={index}>{item}</p>
        ))}
      </div>

      <div className={styles.users__content}>
        <ul>
          {new Array(10).fill(0).map((item, index) => (
            <UsersCard key={index} />
          ))}
        </ul>
        {loading ? (
          <div className={styles.adminsList__loader}>
            <Loader />
          </div>
        ) : (
          !loadMoreAdmins && (
            <Button
            // onClick={loadMoreBtnHandler}
            >
              <span>Load more</span>
            </Button>
          )
        )}
      </div>
    </div>
  );
};

export default Users;
