//-----------Basic imports----------------------------
import React from "react";
import styles from "./PROs.module.scss";

//-----------Libraries----------------------------
import {
  NavLink,
  Route,
  Switch,
  Redirect,
  useLocation,
} from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import clsx from "clsx";

//-----------Routes----------------------------
import ROUTES from "../../route/routes";

//---------------Subpages---------------------------
import PROsRequests from "./PROsRequests";
import AllPROs from "./AllPROs";

//---------------Components------------------------------
import { FiltersModal, SearchInput, SelectInput } from "../../components";

//---------------Assets------------------------------
import ICONS from "../../assets/icons";

const PROs = () => {
  //-----------Hooks----------------------------

  const { loading } = useSelector((state) => state.authReducer);
  const dispatch = useDispatch();

  const [showFilters, setShowFilters] = React.useState(false);

  const location = useLocation().pathname;

  //---------------------State-----------------------

  const request = location.includes(ROUTES.PROs.requests);

  //-----------Data----------------------------

  const tabs = [
    {
      name: "All PROs",
      path: ROUTES.PROs.all,
    },
    {
      name: "Approval Requests",
      path: ROUTES.PROs.requests,
    },
  ];

  const infoTabs = [
    "Photo",
    "Full Name",
    "Address",
    "Pro",
    "Category",
    "Revenue",
    "Date",
    "Rating",
  ];

  //-----------Handlers----------------------------

  const filtersBtnHandler = () => setShowFilters((prev) => !prev);

  //-----------Layout------------------------------

  return (
    <>
      <div className={styles.PROs}>
        <div className={styles.PROs__header}>
          <div className={styles.PROs__nav}>
            {tabs.map((tab, index) => (
              <NavLink
                className={styles.PROs__navItem}
                to={tab.path}
                key={index}
              >
                {tab.name}
              </NavLink>
            ))}
          </div>
          <div className={styles.PROs__filters}>
            <SelectInput
              options={["Date", "Revenue", "Votes"]}
              title="Sort by"
            />
            <SearchInput placeholder="Search in PROs" />
            <button onClick={filtersBtnHandler}>
              {ICONS.utils.filters}
              Filters
            </button>
          </div>
        </div>

        <div className={styles.PROs__infoBar}>
          {infoTabs.map((item, index) => (
            <p className={clsx(request && "hide")} key={index}>
              {item}
            </p>
          ))}
        </div>

        <Switch>
          <Route exact path={ROUTES.PROs.page}>
            <Redirect to={ROUTES.PROs.all} />
          </Route>
          <Route path={ROUTES.PROs.all} component={AllPROs} />
          <Route path={ROUTES.PROs.requests} component={PROsRequests} />
        </Switch>
      </div>
      <FiltersModal show={showFilters} close={filtersBtnHandler} />
    </>
  );
};

export default PROs;
