//-----------Basic imports----------------------------
import React from "react";
import styles from "./AddAdmin.module.scss";

//-----------Libraries----------------------------
import { useSelector, useDispatch } from "react-redux";
import clsx from "clsx";
import { Formik } from "formik";

//-------------Validations-----------------------------
import VALIDATIONS from "../../../validation";

//------------Components-----------------------------
import { Button, Input, Loader } from "../../../components";

//------------APIs-----------------------------
import { createAdmin } from "../../../APIs/admins";

const AddAdmin = () => {
  //-----------Hooks----------------------------

  const { loading, success } = useSelector((state) => state.adminsReducer);
  const dispatch = useDispatch();

  //----------Handlers------------------------------

  const submitHandler = (values) => {
    dispatch(
      createAdmin({
        login: values.login,
        password: values.password,
      })
    );
  };

  React.useEffect(() => {
    if (success === true) {
      document.querySelector("#createAdminForm").reset();
    }
  }, [success]);

  //-----------Layout----------------------------

  return (
    <Formik
      validateOnBlur={false}
      validateOnChange={false}
      initialValues={{ login: "", password: "", confirmPassword: "" }}
      validationSchema={VALIDATIONS.addAdminSchema}
      onSubmit={submitHandler}
    >
      {({ errors, values, setFieldValue, handleSubmit }) => (
        <form
          id="createAdminForm"
          onSubmit={handleSubmit}
          className={styles.addAdmin}
        >
          <Input
            formHelper
            label="Login"
            name="login"
            placeholder="Enter login"
            value={values.login}
            error={errors.login}
            onChange={setFieldValue}
          />
          <Input
            formHelper
            type="password"
            label="Password"
            name="password"
            placeholder="Enter password"
            value={values.password}
            error={errors.password}
            onChange={setFieldValue}
          />
          <Input
            formHelper
            type="password"
            label="Confirm password"
            name="confirmPassword"
            placeholder="confirm your password"
            value={values.confirmPassword}
            error={errors.confirmPassword}
            onChange={setFieldValue}
          />
          {loading ? (
            <div className={styles.addAdmin__loader}>
              <Loader />
            </div>
          ) : (
            <Button type="submit">
              <span>Create admin</span>
            </Button>
          )}
        </form>
      )}
    </Formik>
  );
};

export default AddAdmin;
