import {
  CREATE_ADMINS,
  DELETE_ADMINS,
  GET_ADMINS,
} from "../actions/actionTypes";

const initialSatate = {
  admins: [],
  success: false,
  adminsPage: 1,
  loadMoreAdmins: true,
};

const adminsReducer = (state = initialSatate, action) => {
  switch (action.type) {
    //------------------Create admins--------------------------------------------

    case CREATE_ADMINS: {
      return { ...state, ...action.payload };
    }

    //------------------Delete admins--------------------------------------------

    case DELETE_ADMINS: {
      const { _id } = action.payload;

      let newState;

      if (_id) {
        const newAdmins = state.admins.filter((item) => item._id !== _id);

        newState = {
          ...state,
          admins: newAdmins,
        };
      } else newState = state;

      return newState;
    }

    //------------------Get admins--------------------------------------------

    case GET_ADMINS: {
      const { admins } = action.payload;

      if (admins) {
        return {
          ...state,
          admins:
            state.adminsPage === 1 ? admins : [...state.admins, ...admins],
          adminsPage: 1 + state.adminsPage,
          loadMoreAdmins: admins.length > 0,
        };
      }
      return { ...state, ...action.payload };
    }

    default:
      return state;
  }
};

export default adminsReducer;
