//-----------Basic imports----------------------------
import React from "react";
import styles from "./AdminsList.module.scss";

//-----------Libraries----------------------------
import { useSelector, useDispatch } from "react-redux";

//------------Components-----------------------------
import { Button, Loader } from "../../../components";

//------------APIs-----------------------------
import { deleteAdmin, getAdmins } from "../../../APIs/admins";

//------------Redux action creators-----------------------------
import { getAdminsAction } from "../../../redux/actions/adminsActions";

const AdminCard = ({ data }) => {
  //----------------Hooks--------------------------------

  const [loading, setLoading] = React.useState(false);

  const dispatch = useDispatch();

  //----------------Data--------------------------------

  const { login, _id } = data;

  //----------------Handers--------------------------------

  const deleteHandler = () => dispatch(deleteAdmin(_id, setLoading));

  //----------------Layout--------------------------------

  return (
    <li className={styles.adminsList__item}>
      <p>{login}</p>
      {loading ? (
        <div className={styles.adminsList__loader}>
          <Loader />
        </div>
      ) : (
        <Button onClick={deleteHandler}>
          <span>Delete</span>
        </Button>
      )}
    </li>
  );
};

const AdminsList = () => {
  //-----------Hooks----------------------------

  const { loading, admins, loadMoreAdmins } = useSelector(
    (state) => state.adminsReducer
  );
  const dispatch = useDispatch();

  //-----------Handlers--------------------------

  const loadMoreBtnHandler = () => dispatch(getAdmins());

  React.useEffect(() => {
    dispatch(getAdminsAction({ adminsPage: 1 }));
    dispatch(getAdmins());
  }, []);

  //-----------Layout----------------------------

  return (
    <div className={styles.adminsList}>
      <ul>
        {admins.map((item) => (
          <AdminCard key={item._id} data={item} />
        ))}
      </ul>
      {loading ? (
        <div className={styles.adminsList__loader}>
          <Loader />
        </div>
      ) : (
        loadMoreAdmins && (
          <Button onClick={loadMoreBtnHandler}>
            <span>Load more</span>
          </Button>
        )
      )}
    </div>
  );
};

export default AdminsList;
