//-----------Basic imports----------------------------
import React from "react";
import styles from "./AddCategory.module.scss";

//-----------Libraries----------------------------
import { useSelector, useDispatch } from "react-redux";
import clsx from "clsx";
import { Formik } from "formik";

//-------------Validations-----------------------------
import VALIDATIONS from "../../../validation";

//------------Components-----------------------------
import { Button, Input, Loader } from "../../../components";

//--------------------Assets-------------------------
import ICONS from "../../../assets/icons";

//-------------------APIs----------------------------
import {
  createCategory,
  createSubCategory,
  deleteSubCategory,
  editCategory,
  editSubCategory,
} from "../../../APIs/categories";

//-------------------Redux action creators----------------------------
import { editCategoriesAction } from "../../../redux/actions/categoriesActions";

const SubCategory = ({
  value,
  error,
  name,
  index,
  onDelete,
  changed,
  _id,
  onChange,
}) => {
  //--------------Hooks---------------------------------------------

  const dispatch = useDispatch();
  const { categoryToEdit } = useSelector((state) => state.categoriesReducer);

  const [loading, setLoading] = React.useState(false);

  //--------------Layout--------------------------------------------

  return (
    <div className={styles.addCategory__subcategory}>
      <span className={styles.addCategory__label}>
        Sub category {1 + index}
      </span>
      <div className={styles.addCategory__block}>
        <Input
          formHelper
          placeholder="Enter sub category"
          name={name}
          type="text"
          value={value}
          error={error}
          onChange={
            _id
              ? (name, value) => {
                  onChange(name, { _id: _id, name: value });
                }
              : onChange
          }
        />
        {loading ? (
          <div className={styles.addCategory__subCategoryControls}>
            <Loader />
          </div>
        ) : (
          <div className={styles.addCategory__subCategoryControls}>
            {index > 1 && (
              <button
                onClick={
                  _id
                    ? () => {
                        dispatch(deleteSubCategory(_id, setLoading, onDelete));
                      }
                    : onDelete
                }
                type="button"
              >
                {ICONS.utils.delete}
              </button>
            )}

            {changed && categoryToEdit && (
              <button
                onClick={
                  _id
                    ? () => {
                        dispatch(editSubCategory(_id, setLoading, value));
                      }
                    : () => {
                        dispatch(
                          createSubCategory(
                            categoryToEdit._id,
                            setLoading,
                            {
                              name: value,
                            },
                            (newSubCategory) => {
                              onChange(name, newSubCategory[0]);
                            }
                          )
                        );
                      }
                }
                type="button"
              >
                {ICONS.utils.check}
              </button>
            )}
          </div>
        )}
      </div>
    </div>
  );
};

const AddCategory = () => {
  //-----------Hooks----------------------------

  const { loading, categoryToEdit } = useSelector(
    (state) => state.categoriesReducer
  );
  const dispatch = useDispatch();

  //------------Data---------------------------------

  const initialValues = categoryToEdit
    ? {
        categoryTitle: categoryToEdit.name,
        photo: categoryToEdit.photo,
        subCategories: categoryToEdit.categories,
      }
    : {
        categoryTitle: "",
        photo: null,
        subCategories: ["", ""],
      };

  //----------Handlers------------------------------

  const submitHandler = (values) => {
    if (categoryToEdit)
      dispatch(
        editCategory(
          {
            name: values.categoryTitle,
            photo: values.photo.toString(),
          },
          categoryToEdit._id
        )
      );
    else
      dispatch(
        createCategory({
          name: values.categoryTitle,
          photo: values.photo.toString(),
          subCategories: values.subCategories,
        })
      );
  };

  React.useEffect(() => {
    return () => dispatch(editCategoriesAction({ categoryToEdit: null }));
  }, []);

  //-----------Layout----------------------------

  return (
    <Formik
      validateOnBlur={false}
      validateOnChange={false}
      initialValues={initialValues}
      validationSchema={
        categoryToEdit
          ? VALIDATIONS.editCategorySchema
          : VALIDATIONS.addCategorySchema
      }
      onSubmit={submitHandler}
    >
      {({ errors, values, setFieldValue, handleSubmit }) => (
        <form onSubmit={handleSubmit} className={styles.addCategory}>
          <div className={styles.addCategory__uploadFile}>
            <span className={styles.addCategory__label}>Category icon</span>
            <label htmlFor="file" className={styles.addCategory__uploadInput}>
              <input
                id="file"
                type="file"
                onChange={({ target }) =>
                  target.files[0] && setFieldValue("photo", target.files[0])
                }
              />
              {values.photo ? (
                <img
                  alt="icon"
                  src={
                    typeof values.photo === "string"
                      ? values.photo
                      : URL.createObjectURL(values.photo)
                  }
                />
              ) : (
                <span>Click here</span>
              )}
            </label>
            {errors.photo && (
              <p className={styles.addCategory__uploadFile_error}>
                {errors.photo}
              </p>
            )}
          </div>
          <span className={styles.addCategory__label}>Category title</span>
          <div className={styles.addCategory__block}>
            <Input
              formHelper
              placeholder="Enter category"
              name="categoryTitle"
              type="text"
              value={values.categoryTitle}
              error={errors.categoryTitle}
              onChange={setFieldValue}
            />
            <Button
              onClick={() =>
                setFieldValue("subCategories", [...values.subCategories, ""])
              }
            >
              {ICONS.utils.upload}
              <span>Sub categories</span>
            </Button>
          </div>

          {values.subCategories.map((item, index) => (
            <SubCategory
              key={index}
              onChange={setFieldValue}
              _id={item._id}
              value={
                values?.subCategories &&
                (values?.subCategories[index]?.name ||
                  values?.subCategories[index])
              }
              onDelete={() => {
                setFieldValue(
                  "subCategories",
                  values.subCategories.filter(
                    (item, delIndex) => index !== delIndex
                  )
                );
              }}
              changed={
                categoryToEdit?.categories[index]?.name !==
                (values?.subCategories[index]?.name ||
                  values?.subCategories[index])
              }
              index={index}
              error={errors?.subCategories && errors?.subCategories[index]}
              name={`subCategories[${index}]`}
            />
          ))}
          {loading ? (
            <div className={styles.addCategory__loader}>
              <Loader />
            </div>
          ) : (
            <Button type="submit">
              <span>Save</span>
            </Button>
          )}
        </form>
      )}
    </Formik>
  );
};

export default AddCategory;
