//-----------------Helpers---------------------------------------
import { responseHandler } from "../helpers";

//-----------------Utils---------------------------------------
import fetchRequest from "../utils/fetchRequest";

//-----------------Redux action creators---------------------------------------
import {
  authFinishedAction,
  authProcessAction,
} from "../redux/actions/authActions";
import { showAlertAction } from "../redux/actions/alertActions";

//-------------------Auth--------------------------------------

export const auth = (body) => async (dispatch) => {
  dispatch(authProcessAction());

  const response = await fetchRequest("api/admin/login", "POST", body);

  const error = responseHandler(response, "auth");

  dispatch(authFinishedAction());

  if (!error) {
    localStorage.setItem("accessToken", response.accessToken);
    window.dispatchEvent(new Event("storage"));
  } else {
    dispatch(showAlertAction(error));
  }
};

//-------------------Log out--------------------------------------

export const logOut = () => async (dispatch) => {
  dispatch(authProcessAction());

  const response = await fetchRequest("api/admin/logout", "POST");

  const error = responseHandler(response, "logging out");

  dispatch(authFinishedAction());

  if (!error) {
    localStorage.setItem("accessToken", response.accessToken);
    window.dispatchEvent(new Event("storage"));
  } else {
    dispatch(showAlertAction(error));
  }
};
