//-----------Basic imports----------------------------
import React from "react";
import styles from "./PROsRequests.module.scss";

//-----------Libraries----------------------------
import { useSelector, useDispatch } from "react-redux";

//------------Components-----------------------------
import { Button, Loader, PROsCard } from "../../../components";

//------------APIs-----------------------------

const PROsRequests = () => {
  //-----------Hooks----------------------------

  const { loading, admins, loadMoreAdmins } = useSelector(
    (state) => state.adminsReducer
  );
  const dispatch = useDispatch();

  //-----------Handlers--------------------------

  //-----------Layout----------------------------

  return (
    <div className={styles.PROsRequests}>
      <ul>
        {new Array(10).fill(0).map((item, index) => (
          <PROsCard key={index} />
        ))}
      </ul>
      {loading ? (
        <div className={styles.adminsList__loader}>
          <Loader />
        </div>
      ) : (
        loadMoreAdmins && (
          <Button
          // onClick={loadMoreBtnHandler}
          >
            <span>Load more</span>
          </Button>
        )
      )}
    </div>
  );
};

export default PROsRequests;
