//-----------Basic imports----------------------------
import React from "react";
import styles from "./Categories.module.scss";

//-----------Libraries----------------------------
import { NavLink, Route, Switch, Redirect, useHistory } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import clsx from "clsx";

//-----------Components-----------------------------
import { Loader } from "../../components";

//-----------Routes----------------------------
import ROUTES from "../../route/routes";

//---------------Subpages---------------------------
import CategoriesList from "./CategoriesList";
import AddCategory from "./AddCategory";

//------------------Assets--------------------------
import ICONS from "../../assets/icons";
import { deleteCategory } from "../../APIs/categories";
import { deleteCategoriesAction } from "../../redux/actions/categoriesActions";
import { showAlertAction } from "../../redux/actions/alertActions";

const Categories = () => {
  //-----------Hooks----------------------------

  const { categoryToEdit } = useSelector((state) => state.categoriesReducer);
  const dispatch = useDispatch();

  const [loading, setLoading] = React.useState(false);

  const history = useHistory();

  //-----------Data----------------------------

  const tabs = [
    {
      name: "Categories",
      path: ROUTES.categories.list,
    },
    {
      name: "Add Category",
      path: ROUTES.categories.addCategory,
    },
  ];

  //-----------Handlers----------------------------

  const deleteClickHandler = () =>
    dispatch(
      deleteCategory(categoryToEdit._id, setLoading, () =>
        history.push(ROUTES.categories.list)
      )
    );

  //-----------Layout----------------------------

  return (
    <div className={styles.categories}>
      <div className={styles.categories__nav}>
        {tabs.map((tab, index) => (
          <NavLink
            className={styles.categories__navItem}
            to={tab.path}
            key={index}
          >
            {tab.name}
          </NavLink>
        ))}

        {loading ? (
          <div className={styles.categories__loader}>
            <Loader />
          </div>
        ) : (
          categoryToEdit && (
            <button onClick={deleteClickHandler} type="button">
              {ICONS.utils.delete}
            </button>
          )
        )}
      </div>
      <Switch>
        <Route exact path={ROUTES.categories.page}>
          <Redirect to={ROUTES.categories.list} />
        </Route>
        <Route path={ROUTES.categories.list} component={CategoriesList} />
        <Route path={ROUTES.categories.addCategory} component={AddCategory} />
      </Switch>
    </div>
  );
};

export default Categories;
