//---------------------Basic imports-----------------------
import React from "react";
import "./UsersCard.scss";

//--------------------Assets----------------------------
import ICONS from "../../assets/icons";
import { ReviewsModal } from "..";

const UsersCard = ({}) => {
  //---------------------Hooks-----------------------

  const [state, setState] = React.useState({
    loading: false,
    showReviews: false,
  });

  //--------------------State-----------------------

  const { showReviews } = state;

  //---------------------Handlers--------------------

  const deleteClickHandler = () => {};

  const reviewsClickHandler = () =>
    setState((prev) => ({ ...prev, showReviews: !prev.showReviews }));

  //---------------------Layout-----------------------

  return (
    <>
      <li className="usersCard">
        <div>Christiano Ronaldo</div>
        <div>
          4441 Collins Ave, Miami, FL 331324441 Collins Ave, Miami, FL 33132
        </div>
        <div>+111 111 111 11</div>
        <div>ChristianoRonaldo@gmail.com</div>
        <div>17/11/12</div>
        <div onClick={reviewsClickHandler}>
          5.0 <br /> (145 votes)
        </div>
        <div>
          <button onClick={deleteClickHandler} type="button">
            {ICONS.utils.delete}
          </button>
        </div>
      </li>
      <ReviewsModal show={showReviews} close={reviewsClickHandler} />
    </>
  );
};

export default UsersCard;
