import { AUTH_IN_PROCESS, AUTH_FINISHED, LOG_OUT } from "./actionTypes";

export const authProcessAction = (payload) => ({
  type: AUTH_IN_PROCESS,
  payload,
});

export const authFinishedAction = (payload) => ({
  type: AUTH_FINISHED,
  payload,
});

export const logOutAction = (payload) => ({
  type: LOG_OUT,
  payload,
});
