//-----------------Helpers---------------------------------------
import { responseHandler } from "../helpers";

//-----------------Utils---------------------------------------
import fetchRequest from "../utils/fetchRequest";

//-----------------Redux action creators---------------------------------------
import {
  getAdminsAction,
  createAdminsAction,
  deleteAdminsAction,
} from "../redux/actions/adminsActions";
import { showAlertAction } from "../redux/actions/alertActions";

//-------------------Get admins--------------------------------------

export const getAdmins = () => async (dispatch, getState) => {
  dispatch(getAdminsAction({ loading: true }));

  const { adminsPage } = getState().adminsReducer;

  const response = await fetchRequest(
    `api/admin/profile/list?page=${adminsPage}`,
    "GET"
  );

  const error = responseHandler(response);

  dispatch(getAdminsAction({ loading: false }));

  if (!error) {
    dispatch(getAdminsAction({ admins: response.profiles }));
  }
};

//-------------------Create admin--------------------------------------

export const createAdmin = (body) => async (dispatch) => {
  dispatch(createAdminsAction({ loading: true, success: false }));

  const response = await fetchRequest("api/admin/register", "POST", body);

  const error = responseHandler(response, "creating admin");

  dispatch(createAdminsAction({ loading: false }));

  if (!error) {
    dispatch(
      createAdminsAction({
        loading: false,
        success: true,
      })
    );
    dispatch(showAlertAction("Succesfuly created a new admin!"));
  } else {
    dispatch(showAlertAction(error));
  }
};

//-------------------Delete admin--------------------------------------

export const deleteAdmin = (_id, setLoading) => async (dispatch) => {
  setLoading(true);

  const response = await fetchRequest(`api/admin?id=${_id}`, "DELETE");

  const error = responseHandler(response, "deleting admin");

  setLoading(false);

  if (!error) {
    dispatch(
      deleteAdminsAction({
        _id: _id,
      })
    );
    dispatch(showAlertAction("Succesfuly deleted an admin!"));
  } else {
    dispatch(showAlertAction(error));
  }
};
